<template>
	<view>
		<web-view ref="outweb" :src="url" :progress="false"></web-view>
	</view>
</template>

<script>
	export default {
		data() {
			return {
				url: '',
			}
		},
		onLoad(val) {
			console.log(val)
			this.url = decodeURIComponent(val.url) //解码网址
			
			// this.url = 'http://www.aishangshi689.com/api/go'
			// console.log(this.getQuery(this.url))
			// var my = this.$refs.outweb
			// console.log(this.$refs)
			// this.postQuery()
		},
		methods: {
			postQuery() {
				var query = {
					jsApiParameters: {
						appId: "wx06ecdbdc7b4ae251",
						timeStamp: "1593741253",
						nonceStr: "5efe8fc529f85",
						package: "prepay_id=wx030954131429027a743a63631040903500",
						signType: "MD5",
						paySign: "731F4D65E242A859DF00E5E96AA41C61"
					},
					order_sn: "2020070310200728744"
				}
				uni.request({
					url: 'http://www.aishangshi689.com/api/go',
					method: 'POST',
					data: JSON.stringify(query),
					header: {
						'content-type': 'multipart/form-data'
					},
					success: (res) => {
						console.log(res)
					}
				});
			},
			getQuery(name) {
				var reg = new RegExp("(^|&)" + name + "=([^&]*)(&|$)");
				var r = window.location.search.substr(1).match(reg);
				if (r != null) return unescape(r[2]);
				return null;
			}
		}
	}
</script>

